import { useStaticQuery, graphql } from "gatsby"

export const useShopProducts = () => {
  const { allMdx } = useStaticQuery(
    graphql`
      query shopProducts {
        allMdx(
          filter: { slug: { regex: "/shop//" } }
          sort: { fields: slug, order: ASC }
        ) {
          edges {
            node {
              id
              slug
              excerpt
              body
              frontmatter {
                title
                categories
                slug
                tags
                cover {
                  childImageSharp {
                    fluid(maxWidth: 600) {
                      ...GatsbyImageSharpFluid_withWebp
                    }
                  }
                }
                extra {
                  amount
                  link
                  mail
                  phone
                  price
                  server
                  vehicles {
                    premium
                    x
                  }
                }
              }
            }
          }
        }
      }
    `
  )
  return allMdx.edges
}
